.select_scale {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  align-items: center;

  &_title {
    font-family: 'Nunito', sans-serif;
    font-size: 22px;
    font-weight: 200;
    text-align: center;
    margin: 10px auto 10px auto;
    display: block;
  }

  &_down {
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media (max-width: 750px) {
      flex-direction: row;
    }
  }

  &_item {
    height: 50px;
    background-color: white;
    width: 120px;
    border: 1px solid black;
    margin: 3px;

    @media(max-width: 750px) {
      height: 40px;
    }

    &:hover {
      cursor: pointer;
    }

    &_name {
      background-color: white;
      margin: 0 auto;
      text-align: center;
      line-height: 48px;

      @media(max-width: 750px) {
        line-height: 40px;
      }
    }
  }
}

.active_scale {
  outline: 1px solid darkred;
  color: green;
  background-color: lightgrey;
}
